* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #F1F6EC;
  margin: 0 10rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Unbounded', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  -webkit-animation: all 0.2s ease-out;
          animation: all 0.2s ease-out;
}

.card:hover {
  border: 4px solid #7ECC66;
  background-color: #7ECC66;
  border-radius: 2.4rem;
}

.category-btn {
  -webkit-animation: all 0.2s ease-out;
          animation: all 0.2s ease-out;
}

.category-btn:hover {
  opacity: 0.8;
}

a {
  color: #1F1F1F;
  text-decoration: none;
  margin: 0 0.5rem;
  text-align: center;
}

/* Small devices (landscape phones, 576px and up) */

@media (min-width: 300px) {

  .card, .card-pop {
    height: 6rem;
    width: 6rem;
  }

  span {
    font-size: 1.5rem;
  }

  .category-btn {
    min-width: 3rem;
    min-height: 3rem;
    font-size: 0.5rem;
  }

  .category-btn span {
    font-size: 0.4rem;
  }

  .card, .card-pop {
    height: 4rem;
    width: 4rem;
  }

  p {
    font-size: 0.4rem;
  }

  .slide-container h2 {
    font-size: 1rem;
  }
}

@media (min-width: 576px) {
  .card, .card-pop {
    height: 8rem;
    width: 8rem;
  }

  p {
    font-size: 0.8rem;
  }

  .category-btn {
    width: 6rem;
    height: 6rem;
  }

  .category-btn span {
    font-size: 0.8rem;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (max-width: 580px) {
  .card, .card-pop {
    height: 6rem;
    width: 6rem;
  }

  .recipe-title {
    font-size: 0.5rem;
  }

  span {
    font-size: 2rem;
  }

  .category-btn {
    width: 4rem;
    height: 4rem;
  }

  .category-btn span {
    font-size: 0.6rem;
  }

  .search-form {
    margin: 0 2rem;
  }

  .slide-container h2 {
    text-align: center;
  }

  body {
    width: 100%;
    padding: 0;
    margin: 0;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 920px) {
  .card, .card-pop {
    height: 14rem;
    width: 14rem;
  }

  span {
    font-size: 2.6rem;
  }
}

@media (min-width: 1200px) {
  .card {
    height: 18rem;
    width: 18rem;
  }
}

